import * as React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"

// 要修改 id！改成这个类别下的第一个mdx文件的id
export const query = graphql`
  query GitPageQuery {
    mdx(id: { eq: "1c728508-94ef-5d2c-a888-837adcd07f76" }) {
      body
      id
      tableOfContents(maxDepth: 2)
      slug
      frontmatter {
        title
        date
        description
        group
      }
    }
    allMdx(sort: { fields: frontmatter___path, order: ASC }) {
      group(field: frontmatter___group) {
        edges {
          node {
            frontmatter {
              path
              title
              parentTitle
              pageTitle
              group
            }
            id
          }
        }
      }
    }
  }
`

const GitIndexPage = ({ data }) => {
  // 左侧目录数据
  const thisPageGroup = data.mdx.frontmatter.group
  let sidebarData = {}
  data.allMdx.group.forEach(j => {
    const nodesGroup = j.edges[0].node.frontmatter.group

    if (thisPageGroup === nodesGroup) {
      sidebarData = j.edges
    }
  })

  // 中间内容
  const contentData = data.mdx

  // 右侧目录
  const catalogue = contentData.tableOfContents.items || []

  const seo = {
    title: data.mdx.frontmatter.title,
    description: data.mdx.frontmatter.description,
  }

  return (
    <Layout
      sidebarData={sidebarData}
      contentData={contentData}
      catalogue={catalogue}
      seo={seo}
    ></Layout>
  )
}

export default GitIndexPage
